header {
	padding-top: constant(safe-area-inset-top);
    padding-top: env(safe-area-inset-top);
}

footer {
	padding-bottom: constant(safe-area-inset-bottom);
    padding-bottom: env(safe-area-inset-bottom);
}

.container-fluid {
	margin-top: 0px !important;
	padding: 0px !important;
}

.card {
	margin-bottom: 0px !important;
	border: none !important;
	border-radius: 0px !important
}

.card-header {
	border-radius: 0px !important
}

.card-body {
	padding: 1px !important;
}

.col,
.col-sm-12,
.col-md-6,
.col-lg-4 {
	padding: 0px !important;
}

.table {
	margin-bottom: 0rem !important;
}

.grupo {
	background-color: #FFFFFF;
}

.rodada {
	display: grid;
	grid-template-columns: 10fr 35px 30px 10px 30px 35px 10fr;
	grid-template-rows: 40px;
	grid-column-gap: 2px;
	grid-row-gap: 2px;
	-ms-flex-align: center;
	    align-items: center;
}

.nomeTimeA {
	justify-self: end;
}

.bandeiraTimeA {
	justify-self: center;
	margin-bottom: 0px
}

.palpiteTimeA {
	justify-self: center;
	padding: 2px;
	text-align: center;
}

.divisorPalpite {
	justify-self: center;
}

.palpiteTimeB {
	justify-self: center;
	padding: 2px;
	text-align: center;
}

.bandeiraTimeB {
	justify-self: center;
	margin-bottom: 0px;
}

.nomeTimeB {
	justify-self: start;
}

.horaPartida {
	justify-self: center;
	font-size: 0.7rem;
	grid-column: 1/8;
}


